import React, { useEffect } from "react";
import { Divider } from "antd";
import styled from "styled-components";
import Flower from "../assets/flower2.png";
import yakdo from "../assets/yakdo.png";

const Wrapper = styled.div`
  padding-top: 42px;
  width: 70%;
  margin: 0 auto;
`;

const Title = styled.span`
  font-size: 1rem;
  color: var(--title-color);
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
`;

const Image = styled.img`
  display: block;
  margin: 0 auto;
  width: 1.375rem;
  padding-bottom: 42px;
`;

const Content = styled.p`
  font-size: 0.875rem;
  line-height: 1.75;
  opacity: 0.75;
  width: 100%;
  text-align: center;
  padding-top: 42px;
  padding-bottom: 42px;
  margin: 0;
`;

const Map = styled.div`
  width: 100%;
  padding: 0;
`;

const Location = () => {
  // 카카오 맵 불러오기

  // <!-- 3. 실행 스크립트 -->
  const executeScript = () => {
    const scriptTag = document.createElement("script");
    const inlineScript = document.createTextNode(`new daum.roughmap.Lander({
    "timestamp" : "1720957717407",
    "key" : "2k2b5",
    "mapWidth" : "640",
    "mapHeight" : "360"
  }).render();`);
    scriptTag.appendChild(inlineScript);
    document.body.appendChild(scriptTag);
  };

  // <!-- 2. 설치 스크립트 * 지도 퍼가기 서비스를 2개 이상 넣을 경우, 설치 스크립트는 하나만 삽입합니다. -->
  // document.write 문제가 발생해서 해당 파일을 직접 가져온다음 수정했음
  const InstallScript = () => {
    (function () {
      let c = window.location.protocol === "https:" ? "https:" : "http:";
      let a = "16137cec";

      if (window.daum && window.daum.roughmap && window.daum.roughmap.cdn) {
        return;
      }
      window.daum = window.daum || {};
      window.daum.roughmap = {
        cdn: a,
        URL_KEY_DATA_LOAD_PRE: c + "//t1.daumcdn.net/roughmap/",
        url_protocal: c,
      };
      let b =
        c +
        "//t1.daumcdn.net/kakaomapweb/place/jscss/roughmap/" +
        a +
        "/roughmapLander.js";

      // document.write -> doumnet.body.append로 수정
      const scriptTag = document.createElement("script");
      scriptTag.src = b;
      document.body.append(scriptTag);
      scriptTag.onload = () => {
        executeScript();
      };
    })();
  };

  useEffect(() => {
    InstallScript();
  }, [InstallScript]);

  return (
    <Wrapper>
      <Divider plain style={{ marginTop: 0, marginBottom: 32 }}>
        <Title>오시는 길</Title>
      </Divider>
      <Image src={Flower} />
      <Map
        id="daumRoughmapContainer1720957717407"
        className="root_daum_roughmap root_daum_roughmap_landing"
      ></Map>
      <Content>
        서울 용산구 서빙고로 185
        <br />
        용산가족공원 야외예식장
        <br />
        <br />
        <Title>약도 - 이렇게 오시면 편해요!</Title>
        <img src={yakdo} style={{ width: "95%" }} />
        <br />
        <br />
        <Title>대중교통 이용시(권장)</Title>
        <br />
        이촌역 2번 출구 하차
        <br />
        용산가족공원 야외예식장 도보 10분
        <br />
        <br />
        <Title>자차 이용시</Title>
        <br />
        국립중앙박물관 주차장 이용(유료)
        <br />
        용산가족공원 야외예식장 도보 5분<br />
        <br />
        <Title>안내</Title>
        <br />
        드레스 코드는 소풍입니다. <br />
        공원으로 놀러 가는 기분으로<br />
        편안한 옷을 입어주세요.<br />
        화환은 정중히 사양합니다.<br /> 마음은 감사히 받겠습니다. <br /><br />
        <Title>피로연 안내</Title>
        <br />
        야래향: 용산구 이촌로 75길 22-5, 2층<br />
        자차: 이촌역 올리브영 인근 공영주차<br />
        버스: 용산가족공원 주차장 앞<br />
        육교에서 버스 승차<br />
        12시 10분, 13시 10분 두 차례 운행
        <br />
      </Content>
    </Wrapper>
  );
};

export default Location;
